import styled from 'styled-components';



export const StyledHeader = styled.header`
background-color: #345677;;

 h1 {
   font-family: sans-serif;
   font-size: xx-large;
   background-color: red;
 }

 h3 {
  background-color: white;
 }

 h4 {
  background-color: midnightblue;
  color: white;
 }

 h5 {
   color: white;
 }

`;

export const StyledBody = styled.body`
background-color: lightblue;

h2 {
  font-family: Arial;
}

p > p {
  font-family: Arial;
  font-size: 0.7em;
}

h4 > button {
  background: palevioletred;
  color: white;
  font-size: 1em;
  margin-left: 1em;
  margin-top: -0.5em;
  padding: 0.25em 1em;
  border: 2px solid palevioletred;
  border-radius: 3em;
  ;
}

h4 > button:hover {
  background: violet;
  border-color: violet;
  ;
}

`;

export const StyledFooter = styled.footer`
background-color: lightgray;

div > div {
  display: flex;
}

h1 {
  font-family: Helvetica;
  font-style: oblique;
}

h3 {
  font-family: sans-serif;
}

h4 > button {
  background: palevioletred;
  color: white;
  font-size: 1em;
  margin-left: 1em;
  margin-top: -0.5em;
  padding: 0.25em 1em;
  border: 2px solid palevioletred;
  border-radius: 3em;
  ;
}

h4 > button:hover {
  background: violet;
  border-color: violet;
  ;
}

`;